let SelectList = [];

const LoadingSpinner = () => {
    return (
        <AdminPageContainer style={{ width: "100vw", height: "90vh" }}>
            <SpinLoader></SpinLoader>
        </AdminPageContainer>
    );
};

const LoadAndNotLoginRender = ({ history }) => {
    const [Timer, setTimer] = useState(false);

    useEffect(() => {
        const LoginWaitUnSubscribe = setTimeout(() => setTimer(true), 5000);
        const RedirectWaitUnSubscribe = setTimeout(
            () => history.push("/login"),
            7000
        );

        return () => {
            clearTimeout(LoginWaitUnSubscribe);
            clearTimeout(RedirectWaitUnSubscribe);
        };
    }, []);

    if (Timer) {
        return <div>Í¥ÄÎ¶¨Ïûê ÌéòÏù¥ÏßÄÎäî Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§!</div>;
    } else {
        return <LoadingSpinner />;
    }
};

const RenderLists = ({ id, item, history, tabColor }) => {
    const [isCheck, setIsCheck] = useState(false);

    function onClickEvent(prev, id) {
        setIsCheck((prev) => {
            return !prev;
        });

        if (prev === false) {
            SelectList.push(id);
        } else {
            const find_index = SelectList.indexOf(id);
            SelectList.splice(find_index, 1);
        }
    }

    return (
        <ListContent
            tabColor={tabColor}
            onClick={() => {
                onClickEvent(isCheck, id);
            }}
        >
            <CheckBox isCheck={isCheck} />
            {item}
            <MoveToDetail
                onClick={() => {
                    history.push(`/detail/${id}`);
                }}
            >
                ‚Üí
            </MoveToDetail>
        </ListContent>
    );
};

