export default function AdminPage() {
    const db = useDb();
    const auth = useAuth();
    const storage = useStorage();
    const [isVisible, setIsVisible] = useState(false);
    const [message, setMessage] = useState("");
    const [DataList, setDataList] = useState(null);

    const history = useHistory();

    const fetchData = async () => {
        const data = await db.getContentList();
        setDataList(data);
    };

    useEffect(() => {
        SelectList = [];
        setTimeout(() => {
            fetchData();
        }, 1000);
    }, []);

    async function checkModalRender(list, type = "edit") {
        if (list.length === 0) {
            setMessage("ÏÑ†ÌÉùÌïú Ìï≠Î™©Ïù¥ ÏóÜÏäµÎãàÎã§. ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî!");
            setIsVisible((prev) => !prev); // modalÏ∞Ω Î≥¥Ïó¨Ï£ºÎäî Î∂ÄÎ∂Ñ
        } else if (list.length >= 2 && type === "edit") {
            setMessage("ÏàòÏ†ïÌïòÏã§ 1Í∞ú Í≤åÏãúÎ¨ºÎßå ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî!");
            setIsVisible((prev) => !prev);
        } else {
            // Ï°∞Í±¥Ïóê Î¨∏Ï†úÍ∞Ä ÏóÜÏùÑ Îïå Ï≤òÎ¶¨ => TODO : ÏÇ≠Ï†ú ÎêòÏóàÏùÑ Îïå, Î¶¨Ïä§Ìä∏Î•º Îã§Ïãú Î∂àÎü¨ÏôÄÏïºÌï®. => Done
            // Î¶¨Ïä§Ìä∏ Î∂àÎü¨Ïò§ÎäîÍ±∞ Ï≤òÎ¶¨ Ïñ¥ÎñªÍ≤å Ìï¥ÏïºÌï®. => Done
            if (type === "delete") {
                // ÏÇ≠Ï†úÎ≤ÑÌäº ÎàÑÎ•¥Îäî Í≤ΩÏö∞ Ï≤òÎ¶¨ÏÇ¨Ìï≠.
                await db.deleteContent(list);
                await storage.deleteMdfile(list);

                setTimeout(() => {
                    fetchData();
                }, 1000);
            } else {
                // ÏàòÏ†ïÎ≤ÑÌäº ÎàÑÎ•¥Îäî Í≤ΩÏö∞ Ï≤òÎ¶¨ÏÇ¨Ìï≠.
                history.push(`/edit/${list[0]}`);
            }
        }
    }

    if (auth.user) {
        return (
            <AdminPageContainer
                onClick={(event) => {
                    if (
                        isVisible &&
                        !event.target.classList.contains("modal-class")
                    ) {
                        setIsVisible((prev) => !prev);
                    }
                }}
            >
                <ButtonContainer>
                    <WriteButton
                        onClick={() => {
                            history.push("/write");
                        }}
                    >
                        <span>Í∏Ä Ïì∞Í∏∞</span>
                    </WriteButton>
                    <EditButton
                        onClick={(event) => {
                            checkModalRender(SelectList);
                            event.preventDefault();
                        }}
                    >
                        <span>Í∏Ä ÏàòÏ†ï</span>
                    </EditButton>
                    <DeleteButton
                        onClick={(event) => {
                            checkModalRender(SelectList, "delete");
                        }}
                    >
                        <span>Í∏Ä ÏÇ≠Ï†ú</span>
                    </DeleteButton>
                </ButtonContainer>

                <ListContainer>
                    {DataList &&
                        DataList.map((item) => {
                            return (
                                <RenderLists
                                    key={item.id}
                                    id={item.id}
                                    item={item.data().title}
                                    history={history}
                                    tabColor={DataList.indexOf(item) % 2}
                                />
                            );
                        })}
                </ListContainer>
                <ModalPortal>
                    <Modal isVisible={isVisible} message={message} />
                </ModalPortal>
            </AdminPageContainer>
        );
    } else {
        return <LoadAndNotLoginRender history={history} />;
    }
}
